}
.app-header__title {
    font-size: 1.5rem;
    font-weight: 600;
    margin-right: .75rem;
    line-height: 1;
}
.explorer-toggle {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 3px;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.25);
  text-indent: 2rem;
  overflow: hidden;
}
.explorer-toggle:focus {
    outline: none;
}
.explorer-toggle--active {
    z-index: 2;
    border-color: #9c3;
    background-color: #9c3;
}
.explorer-arrow {
  position: fixed;
  width: 1.5rem;
  height: .75rem;
  background-color: #fff;
  z-index: 3;
  -webkit-clip-path: polygon(0% 100%, 50% 25%, 100% 100%);
          clip-path: polygon(0% 100%, 50% 25%, 100% 100%);
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.page-info {
  position: fixed;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
  color: #888;
}
.page-info strong {
    color: #5eb0e5;
}
.loading {
  position: fixed;
  top: calc(50% - 1rem);
  left: calc(50% - 1rem);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border-width: .25rem;
  border-style: solid;
  border-color: #9c3 transparent transparent transparent;
  -webkit-animation: spin .5s linear infinite;
          animation: spin .5s linear infinite;
}
@-webkit-keyframes spin {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

